.contactUsContainer{
    background-image: url(https://images01.nicepage.io/c461c07a441a5d220e8feb1a/18c35c924140510897caf9fa/xc-min.jpg);
    height: 600px;
    background-size: cover;
    background-position: bottom;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
 .contactFilter{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.4;
}
.contactLeftDiv{
    position: relative;
    width: 40%;
    height:536px;
    color: white;
    text-align: left;
}
.contactLeftDiv h1{
    font-size:40px
}
.contactLeftDiv p{
    font-size: 25px;
    letter-spacing: 1px;
}
.contactLeftDiv button{
    width: 150px;
    height: 50px;
    background: none;
    color: white;
    cursor: pointer;
    letter-spacing: 2px;
    font-weight: bold;
    margin-top: 25px;
    border: solid 2px;
}
.contactLeftDiv button:hover{
    background-color: orange;
}
.contactRightDiv{
    width: 40%;
    color: white;
    font-weight: bold;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.contactRightDiv .email , .name {
    display: grid;
    text-align: left;
    width: 45%;
}
.email input, .name input{
height:30px
}
.contactRightDiv .address{
    width: 90%;
    text-align: left;
    margin: 30px 0px;
}
.contactRightDiv .address input{
    width: 100%;
    height: 30px;
}
.contactRightDiv .message{
    display: grid;
    width: 90%;
    text-align: left;
}
.contactRightDiv .message textarea{
    height: 150px;
}
.contactRightDiv .message button{
    width: 150px;
    height: 50px;
    margin: 30px 0px;
    letter-spacing: 2px;
    cursor: pointer;
}
@media(max-width:700px){
    .contactLeftDiv p {
        font-size: 20px;
        letter-spacing: 1px;
    }
    .contactRightDiv .email , .name {
        width: 90%;
        margin-top: 30px;
    }
}
@media(max-width:1000px){
    .contactUsContainer {
        background-image: url(https://images01.nicepage.io/c461c07a441a5d220e8feb1a/18c35c924140510897caf9fa/xc-min.jpg);
        background-size: cover;
        background-position: bottom;
        height: auto;
        position: relative;
        display: inline-block;
    }
    .contactLeftDiv{
        position: relative;
        width: 70%;
        height: 536px;
        color: white;
        text-align: center;
        margin: 0 auto;
    }
    .contactRightDiv{
        width: 70%;
        color: white;
        font-weight: bold;
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin: 0 auto;
    }
}